
.game.game-paused .board {
	background-color: #eee;
}



.board {
	border: 4px solid #e1e1e1;
	display: table;
	margin: 2em auto;
	padding: 1px;
}

.board.board-lost {
	border-color: #f34d4d;
	-webkit-animation: border-flash 0.25s linear 0s 3;
	        animation: border-flash 0.25s linear 0s 3;
}

.board.board-won {
	border-color: #65de54;
}

.board-row {
	display: table-row;
}

.board-header {
	display: table-caption;
	position: relative;
	height: 3.5em;
	margin-bottom: 5px;
}

.board-header-item {
	position: absolute;
	bottom: 0;
	width: 50%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	border: 1px solid #ccc;
	font-size: 2em;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.board-header-item.mines-remaining {
	left: 0;
	color: #fff;
	background: -webkit-gradient(linear, left top, left bottom, from(#f34d4d), to(#e01310));
	background: -webkit-linear-gradient(top, #f34d4d, #e01310);
	background: -o-linear-gradient(top, #f34d4d, #e01310);
	background: linear-gradient(to bottom, #f34d4d, #e01310);
	padding-left: 1em;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.board-header-item.mines-remaining.success {
	background: -webkit-gradient(linear, left top, left bottom, from(#65de54), to(#38b027));
	background: -webkit-linear-gradient(top, #65de54, #38b027);
	background: -o-linear-gradient(top, #65de54, #38b027);
	background: linear-gradient(to bottom, #65de54, #38b027);
}

.board-header-item.time-elapsed {
	right: 0;
	color: #777;
	background: -webkit-gradient(linear, left top, left bottom, from(#eee), to(#ccc));
	background: -webkit-linear-gradient(top, #eee, #ccc);
	background: -o-linear-gradient(top, #eee, #ccc);
	background: linear-gradient(to bottom, #eee, #ccc);
	padding-right: 1em;
	-ms-flex-pack: end;
	    justify-content: flex-end;
}

.board-header-item-icon {
	margin-right: 0.5em;
	font-weight: bold;
}

@-webkit-keyframes border-flash {
	25% {
		border-color: #f34d4d;
	}
	75% {
		border-color: #e1e1e1;
	}
}

@keyframes border-flash {
	25% {
		border-color: #f34d4d;
	}
	75% {
		border-color: #e1e1e1;
	}
}


